.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;

    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      border-radius: 5px;
      padding: 5px;
      margin: 20px;
      display: flex;

      h1 {
        color: #616161;
        font-size: 20px;
      }

      .left {
        flex: 1;
        text-align: center;

        .image {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          object-fit: cover;
          transform: translateY(100px);
        }
      }

      .right {
        flex: 2;

        form {
          display: flex;
          flex-wrap: wrap;
          gap: 30px;
          justify-content: space-around;

          .formInput {
            width: 40%;

            label {
              display: flex;
              align-items: center;
              gap: 10px;

              .icon {
                cursor: pointer;
              }
            }
            input {
              width: 100%;
              padding: 5px;
              border: none;
              border-bottom: 1px solid gray;
              font-size: 18px;
              outline: none;
              background-color: transparent;

              &::placeholder {
                font-size: 14px;
              }
            }
          }
          button {
            width: 150px;
            padding: 10px;
            margin: 10px 0;
            border: none;
            background-color: #210876;
            color: white;
            font-weight: bold;
            cursor: pointer;
          }
        }
      }
    }
  }
}
